header{
    height: 100vh;
    padding-top: 5rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

/* =========== cta ==============*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    justify-content: center;
    gap: 1.2rem;
}

/* =========== header socials ==============*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    left: 0;
    bottom: 3rem;
    position: absolute;
}
.header__socials::after{
    width: 1px;
    height: 2rem;
    content: '';
    background: var(--color-primary);
}

/* =========== ME ==============*/
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 22rem;
    height: 33rem;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 2rem;
    border-radius: 12rem 12rem 0 0;
   
}
.myimg{
    width: 100%;
    height: 60%;
}
/* ===========scroll down ==============*/
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}


/* ==========================Media Queries (medium devices)================*/
@media screen and (max-width:1024px) 
{
   header{
    height: 68vh;
   }
   .myimg{
    width: 100%;
    height: 100%;
}
}


/* ==========================Media Queries (small devices)================*/
@media screen and (max-width:600px) 
{
    header{
        height: 100vh;
       }
    .header__socials ,.scroll__down{
        display: none;
    }  
    .myimg{
        width: 100%;
        height: 80%;
    } 
}

